
body
{
    box-sizing: border-box;
    --color60: rgb(239, 254, 253);
    --color30: #ffffff;
    --color10: rgb(20, 193, 240);
   
    scrollbar-color: black;
    scrollbar-width: 5px;
    font-size: 24px;
}
*::-webkit-scrollbar
{
    width: 0px;

    
}
*::-webkit-scrollbar-track 
{
    background: orange;        /* цвет дорожки */
}
*::-webkit-scrollbar-thumb 
{
    background-color: blue;    /* цвет плашки */
    border-radius: 20px;       /* закругления плашки */
    border: 3px solid orange;  /* padding вокруг плашки */
}
  
.chat
{
    height: 100%;
    display: flex;
    flex-direction: column;
    
    width: 900px;
    margin: 0 auto;
    
    position: relative;
}
.chat .view
{
    display: flex;
    flex-direction: column-reverse;
    flex-grow: 19;
    background-color: var(--color60);
    margin: 0px;
    border: 2px solid var(--color10);
    border-radius: 50px;
    margin-bottom: 12%;
    overflow-y: auto;
    padding: 20px 0;
}
.chat .send 
{
    height: 50px;
    display: flex;
    flex-direction: row;
    margin: 15px 0px 15px 50px;
    justify-content: space-around;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;

}
.chat .send .msg-field
{
    display: block;
   
    border: 2px solid black;
    border-radius: 30px;
    padding: 0;
    font-size: 24px;
    width: 100%;
    background-color: var(--color60);
    border: none;
    outline: none;
    padding: 0 20px;
}
.chat .send .msg-submit
{
    display: block;
    width: 100px;
    background: url('../public/images/icon/send-button.png');

    background-repeat: no-repeat;
    background-size: contain;
    background-position: 50%;
    margin: 0 1px;
    border: none;
    cursor: pointer;
}
.chat .message
{
    flex-direction: column;
    background-color: var(--color30);
    height: max-content;
    border-radius: 40px;
    margin: 15px 50px;
    padding: 20px 30px;
    display: flex;
}
.chat .author
{
    flex-grow: 10;
    color: var(--color10);
    font-size: 26px;
    width: fit-content;
    height: fit-content;
    left: 0;
    font-weight: 900;

}
.chat .data
{
    font-size: 24px;

    height: fit-content;
    
    width: 95%;
    word-wrap: break-word;
 
}